import { useState, useEffect } from "react";
import { useParams } from "react-router-dom";

import * as imageApi from "../apis/imageApi";
import * as commentApi from "../apis/commentApi";

import DetailImage from "../components/DetailImage";
import CommentForm from "../components/CommentForm";
import TextField from "../components/TextField";
import CommentButton from "../components/CommentButton";
import CommentContainer from "../components/CommentContainer";
import Comment from "../components/Comment";

const ImagePage = () => {
  const { id } = useParams();

  const [image, setImage] = useState(); // 
  const [comments, setComments] = useState(); //  
  const [commentInput, setCommentInput] = useState("");

  useEffect(() => {
    //   API 
    imageApi.getImageById(id).then((image) => {
      setImage(image);
    });
  }, [id]);

  useEffect(() => {
    //   API 
    commentApi.getCommentsByImageId(id).then((comments) => {
      setComments(comments);
    });
  }, [id]);

  //        
  const handleCommentInputChange = (e) => {
    setCommentInput(e.target.value);
  };

  //       
  const handleCommentButtonClick = async () => {
    commentApi.createComment(id, commentInput).then(() => {
      commentApi.getCommentsByImageId(id).then((comments) => {
        setComments(comments);
        setCommentInput("");
      });
    });
  };

  //       
  const handleDeleteClick = (commentId) => {
    commentApi.deleteCommentById(id, commentId).then(() => {
      commentApi.getCommentsByImageId(id).then((comments) => {
        setComments(comments);
        setCommentInput("");
      });
    });
  };

  if (!image || !comments) return " ";

  return (
    <>
      <DetailImage
        image={image.imageUrl}
        title={image.title}
        description={image.description}
      />
      <CommentForm>
        <TextField
          placeholder=" ..."
          value={commentInput}
          onChange={handleCommentInputChange}
        />
        <CommentButton onClick={handleCommentButtonClick}></CommentButton>
      </CommentForm>

      <CommentContainer>
        {comments.map((comment) => (
          <Comment
            key={comment.id}
            author={comment.author}
            body={comment.body}
            onDelete={() => handleDeleteClick(comment.id)}
          />
        ))}
      </CommentContainer>
    </>
  );
};

export default ImagePage;
